.navbar {
  position: relative;
  z-index: 200;

  &-navigation {
    display: flex;

    &-left {
      width: 50vw;
      height: 100vh;
      background-color: $color-dark;
      position: fixed;
      left: 0;

      .left-img {
        width: 50%;
        position: absolute;
        box-shadow: 0 15px 60px rgba($color-black, 0.4);
        opacity: 0.7;
        border-radius: 10px;
      }

      .left-img-1 {
        top: 15%;
        left: 15%;
      }

      .left-img-2 {
        top: 35%;
        left: 25%;
      }

      .left-img-3 {
        top: 55%;
        left: 35%;
      }
    }

    &-right {
      width: 50vw;
      height: 100vh;
      background-color: #1f1d1d;
      position: fixed;
      right: 0;
      @include flexLayout;
    }

    .nav-list {
      &-item {
        list-style: none;
      }

      &-link {
        font-family: $font-dancingScript;
        font-size: 50px;
        color: $color-secondary;
        @include textStyles(capitalize);
        text-decoration: none;
        display: block;
        margin: 20px;
        text-align: center;
        transition: all 0.5s;

        &:hover {
          color: $color-primary;
          letter-spacing: 4px;
        }
      }
    }
  }
}

.header {
  width: 100%;
  height: 100vh;
  position: relative;
  background: linear-gradient(rgba($color-black, 0.8), rgba($color-black, 0.6)),
    url(../images/bg.jpeg) center no-repeat;
  background-size: cover;

  .brand {
    display: flex;
    align-items: center;
    @include centering(15%);
  }

  &-banner {
    text-align: center;
    @include centering(60%);
  }
}

.about-us {
  height: 90vh;
  display: flex;
  align-items: center;

  &-left {
    width: 40%;
    position: relative;
    left: -100px;

    img {
      width: 100%;
      opacity: 0.7;
    }
  }

  &-right {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 100px;
    box-sizing: border-box;

    .stars {
      margin: 30px 0;
    }

    .star {
      font-size: 15px;
      color: $color-primary;
      margin-left: 10px;
    }

    .description {
      font-family: $font-josefinSans;
      font-size: 25px;
      font-style: italic;
      color: $color-secondary;
      text-align: justify;

      &::first-letter {
        padding-left: 50px;
      }
    }
  }
}

.gallery {
  .cards-wrapper {
    display: flex;
    flex-wrap: wrap;
  }
}

.footer {
  padding: 70px 0;
  @include flexLayout;
  flex-direction: column;

  &-header {
    display: flex;
    align-items: center;
    margin-bottom: 70px;
  }

  .social-media {
    display: flex;
    width: 300px;
    justify-content: space-between;
    margin-bottom: 70px;

    &-item {
      list-style: none;
    }

    &-link {
      text-decoration: none;
      font-size: 50px;
      color: $color-secondary;
      transition: 0.4s;

      &:hover {
        color: $color-primary;
      }
    }
  }

  &-copyright-paragraph {
    font-family: $font-josefinSans;
    font-size: 18px;
    color: $color-secondary;
    @include textStyles(capitalize);
  }
}
